type AnyJson = boolean | number | string | null | JsonArray | JsonMap;
interface JsonMap {
  [key: string]: AnyJson;
}
interface JsonArray extends Array<AnyJson> {}

(BigInt.prototype as any).toJSON = function () {
  return this.toString();
};

export function format_locals(locals: string | JsonMap) {
  if (typeof locals !== "string") {
    return JSON.stringify(locals, null, 2);
  }
  return locals;
}

export function format_preview(response: string) {
  const CUTOFF = 100;
  let oneline_response = response.replace(/\n/g, " ");
  if (oneline_response.length > CUTOFF) {
    return oneline_response.slice(0, CUTOFF).trim() + "...";
  } else {
    return oneline_response.trim();
  }
}

export function format_headers(headers: { [key: string]: string }) {
  return Object.entries(headers)
    .sort()
    .map(([key, value]) => {
      return `${key}: ${value}`;
    })
    .join("\n");
}

export function format_return_value(
  value: string | AnyJson,
  pretty: boolean = true
): string {
  if (value === null) {
    return "None";
  } else if (value === false) {
    return "False";
  } else if (value === true) {
    return "True";
  }

  // Backward compatibility
  // Historically, `value` was already formatted json, so we parse
  // and re-encode it.
  if (typeof value === "string") {
    try {
      value = JSON.parse(value);
    } catch (e) {}
  }

  if (pretty) {
    return JSON.stringify(value, null, 2);
  }
  return JSON.stringify(value);
}
