import type {
  InitialData,
  AcquireVsCodeApi,
  TreeNode,
  MessageFromVsCode,
  InitialDataMessage,
  ThemeChangeMessage,
} from "./util/types";

import App from "./components/App.svelte";
import { constructRootNode } from "./util/frames";
import { CSSVarsAccessor } from "./util/css-vars";

declare global {
  interface Window {
    acquireVsCodeApi: AcquireVsCodeApi;
    initialData: InitialData;
  }
}

let app: App | undefined = undefined;
let vscode: ReturnType<AcquireVsCodeApi>;

function init({ initialData }: InitialDataMessage) {
  const { executionTreeNodes, invocationId } = initialData;
  app = new App({
    target: document.body,
    props: {
      cssVars: new CSSVarsAccessor(window),
      frame: constructRootNode({ executionTreeNodes }),
      onFrameClick: (node?: TreeNode): void => {
        if (node?.type === "frame_span") {
          vscode.postMessage({
            command: "goToFrame",
            invocationId,
            frameIndex: node.index,
          });
        } else if (node?.type === "sql_query") {
          vscode.postMessage({
            command: "goToSQLQuery",
            invocationId,
            frameIndex: node.index,
          });
        } else if (node?.type === "outbound_http_request") {
          vscode.postMessage({
            command: "goToOutboundHttpRequest",
            invocationId,
            frameIndex: node.index,
          });
        }
      },
      onFrameFocus: (): void => {
        // do something at some point?
      },
    },
  });
}

function themeChange(message: ThemeChangeMessage) {
  if (app) {
    app.$set({ cssVars: new CSSVarsAccessor(window) });
  }
}

// @ts-ignore – the function is only defined in the vscode context
if (window.acquireVsCodeApi) {
  vscode = window.acquireVsCodeApi();
  window.addEventListener("message", (event) => {
    const message: MessageFromVsCode = event.data; // The JSON data our extension sent
    const { command } = message;
    if (command === "init") {
      init(message);
    } else if (command === "themeChange") {
      themeChange(message);
    } else {
      throw new Error(`Unexpected command: ${(message as any)?.command}`);
    }
  });

  vscode.postMessage({ command: "ready" });
} else {
  // We're running in a web context (not vscode)
  window.addEventListener("web-load-viz", (event) => {
    console.log(event);
    const target = document.getElementById("viz") as HTMLElement;
    target.innerHTML = "";
    // @ts-ignore
    const executionTreeNodes = event.detail.tree.executionTreeNodes;
    // @ts-ignore
    const traceId = event.detail.tree.traceId;
    new App({
      target,
      props: {
        frame: constructRootNode({
          executionTreeNodes,
        }),
        cssVars: new CSSVarsAccessor(window),
        onFrameClick: (node?: TreeNode): void => {},
        onFrameFocus: (): void => {
          // do something at some point?
        },
      },
    });
  });
}
